:root {
  --font-source-sans: var(--font-source-sans);
  --font-inter: var(--font-inter);
}

body {
  font-family: var(--font-source-sans), sans-serif;
  font-weight: 400;
  margin: 0;
  padding: 0;
}
*{box-sizing: border-box;}


strong, b, .bold {font-weight: 700;}
.extra-bold {font-weight: 900;}
em, i, .italic {font-style: italic;}
.bold-italic {font-weight: 700;font-style: italic;}
.light {font-weight: 300;}
.thin {font-weight: 200;}
.thin-italic {font-weight: 200;font-style: italic;}
.text{
  &-white{color: #fff;}
}



h1,.h1,h2,.h2,h3,.h3,h4,.h4,h5,.h5,h6,.h6,p{margin-block-start: 0;font-weight: 500;}
h1,.h1{font-size:3.625rem;line-height:1.25; font-weight:300;}
h2,.h2{font-size: 2.938rem;font-weight: 700;}
h3,.h3{font-size: 1rem;}
h4,.h4{font-size: 1.063rem;}
h5,.h5{font-size: 1rem;margin-block-end: 1.5rem;}
h6,.h6{font-size: 0.813rem;}
p{font-size: 0.875rem;font-weight: 400;line-height:1.6;}
.container{max-width: 90rem;padding-inline: 3.125rem;margin-inline: auto;}





